        this._leftArm.alpha = Math.PI;

        this._rightArm = new Arm(this._waist.obj, -1);

        this._leftArm.obj.position.add(new THREE.Vector3(-dist, .25, 0));
        this._rightArm.obj.position.add(new THREE.Vector3(dist, .25, 0));

