import * as THREE from 'three';

import GlobalVars from "../../data/globalVars";
import Math2 from "../../data/math2";

let da = null;
export default class Arm {

    constructor(scene, d) {
        da = d;
        const _material = new THREE.MeshStandardMaterial();
        _material.color = new THREE.Color(0x64ede2);
        _material.wireframe = false;

        this.alpha = 0;

        //create container object
        this.obj = new THREE.Object3D();
        //this.obj.position.set(0, 3.75, 0);
        /*this.obj.rotation.x = -20 * Math2.deg2rad * d;
        this.obj.position.set(0, 0, 0);*/

        //DEBUG
        //this.obj.add(new THREE.Mesh(new THREE.SphereGeometry(.35, 32, 32), _material));

        //create upper cube
        const _upperArmGeometry = new THREE.BoxGeometry(3, 0.5, 3);
        this._upperLeg = new THREE.Mesh( _upperArmGeometry, _material);
        this._upperLeg.position.add(new THREE.Vector3(-3/2*d, -0.5/2, 0));
        this.obj.add(this._upperLeg);

        //create lower pivot
        this.lowPivot = new THREE.Object3D();
        this.lowPivot.position.set(-3*d, 0, 0);
        //this.lowPivot.rotation.y = -15 * Math2.deg2rad * d;
        this.obj.add(this.lowPivot);
        //this.lowPivot.add(new THREE.Mesh(new THREE.SphereGeometry(.35, 32, 32), _material));

        //create lower cube
        const _lowerLegGeometry = new THREE.BoxGeometry(3, 0.5, 3);
        this._lowerLeg = new THREE.Mesh( _lowerLegGeometry, _material);
        this._lowerLeg.position.add(new THREE.Vector3(-3/2*d, -0.5/2, 0));
        this.lowPivot.add(this._lowerLeg);




        scene.add(this.obj);
    }

    update(){

        //hands
        this.obj.rotation.z = 0.5 * Math.sin(GlobalVars.t + this.alpha);
        //this.lowPivot.rotation.z = 0.75 * Math.sin(GlobalVars.t + this.aplha) + (-10*Math2.deg2rad);
        this.lowPivot.rotation.z = 0.5 * Math.sin(GlobalVars.t + this.alpha) + (-10 * Math2.deg2rad);

    }
}
