import * as THREE from 'three';

import GlobalVars from "../../data/globalVars";
import Math2 from "../../data/math2";
import Camera from './camera';
import Controls from './controls';

let g_app = null;
const mouse = new THREE.Vector2();
var l_mouse = new THREE.Vector2();
var dx = 0;
var camX = 0;


export default class Cinemachine {

    constructor(app) {

        g_app = app;
        this.cam = new Camera(app.renderer.threeRenderer);
        this.cam = this.cam.threeCamera;

        app.man.add(this.cam);
        this.cam.position.set(5, 3, 0);
        this.cam.rotation.x = -15*Math2.deg2rad;
        //this.cam.position.z = 10;
        // this.cam.rotation.y = Math.PI/2;

        this.camGO = new THREE.Object3D();
        this.camGO.position.z = 0;
        this.camGO.position.x = 15;
        this.camGO.position.y = 7;

        this.camGO.rotation.y = Math.PI/2;
        //this.camGO.add(new THREE.Mesh(new THREE.SphereGeometry(1, 32, 32), new THREE.MeshNormalMaterial()));
        this.camGO.add(this.cam);
        app.man.add(this.camGO);




        window.addEventListener('mousemove', mousemove);

    }

    update(){

        this.moveX();


        //this.cam.lookAt(g_app.man_chido._waist.obj.position);



    }

    moveX(){

        camX += Math.sign(dx) * 0.2;
        this.cam.position.x = camX;

        if(camX > 6)
            camX = 6;

        if(camX < -6)
            camX = -6;


        dx = 0;
    }

    
}


function mousemove(e){

    mouse.x = ( event.clientX / window.innerWidth ) * 2 - 1;
    mouse.y = - ( event.clientY / window.innerHeight ) * 2 + 1;

    dx = mouse.x - l_mouse.x;

    l_mouse.x = mouse.x;
    l_mouse.y = mouse.y;
}

var lerp = ( x, y, t ) => ( 1 - t ) * x + t * y;
